const columns = [
  {
    title: 'Cihaz',
    dataIndex: 'Browser',
    width: '20%',
    key: 'browser',
  },
  {
    title: 'Tarih',
    dataIndex: 'IP_ItemListDate',
    Key: 'IP_ItemListDate',

    width: '20%',
    scopedSlots: { customRender: 'IP_ItemListDate' },
  },
  {
    title: 'IP Adresi',
    dataIndex: 'IP_ItemList',
    Key: 'IP_ItemList',
    scopedSlots: { customRender: 'IP_ItemList' },
    width: '20%',
  },
  {
    title: 'Eylem',
    Key: 'actions',

    scopedSlots: {
      customRender: 'actions',
    },
    width: '20%',
  },
  {
    title: '',
    dataIndex: 'xpand',
    Key: 'xpand',
    scopedSlots: {
      customRender: 'xpand',
    },
    width: '20%',
  },
  {
    title: '',
    dataIndex: '',
    width: '5%',
  },
]
const subcolumns = [
  {
    title: '',
    dataIndex: '',
    width: '20%',
    key: 'browser',
  },
  {
    title: '',
    dataIndex: 'CreatedOn',
    key: 'CreatedOn',
    width: '20%',
  },
  {
    title: '',
    dataIndex: 'IP',
    key: 'IP',
    width: '20%',
  },
  {
    title: '',
    dataIndex: '',
    key: 'actions',
    width: '20%',
  },
  {
    title: '',
    dataIndex: '',
    width: '5%',
  },
]
import { isMobile } from 'mobile-device-detect'
export default {
  name: 'Management',
  head(){
    return{
      title: 'Cihaz Yönetimi | Cointral.com',
    }
  },
  data() {
    return {
      columns,
      subcolumns,
      deviceManagementData: [],
      deviceManagementRowKeys: [],
      isMobile,
      visible: [],
      modalVisible: false,
      deneme: false,
    }
  },
  mounted() {
    this.getDeviceManagementData()
  },
  methods: {
    showModal(index) {
      this.modalVisible = true
    },
    handleCancel(e) {
      this.modalVisible = false
    },
    setDeviceManagementRows(expanded, record) {
      if (expanded) {
        this.deviceManagementRowKeys = [record.DeviceId];
      } else {
        this.deviceManagementRowKeys = []
      }
    },
    async getDeviceManagementData() {
      try {
        let authHeaders = {
          Authorization: 'Bearer ' + this.$cookies.get('Token'),
        }

        const response = await this.$axios.get(
          this.$config.FINANCE_API_URL + `/GetUserBrowserList`,
          {
            headers: authHeaders,
          }
        )
        if (response.data) {
          let modalSize = []

          //this.deviceManagementData = response.data.UserBrowser;
          this.deviceManagementData = [
            {
              Browser: "Chrome",
              DeviceId: "fd0bc20cc624dea185ad2d219591cebf",
              IP_ItemList: [
               {
                IP: "202.47.35.63", 
                CreatedOn: "2020-10- 31T05:19:30Z",
               },
               {
                IP: "202.47.35.63", 
                CreatedOn: "2020-10- 31T05:19:30Z",
               },
                {
                IP: "202.47.35.63", 
                CreatedOn: "2020-10- 31T05:19:30Z",
               },
              ]
            },
            {
              Browser: "Chrome",
              DeviceId: "fd0bc20cc624dea185ad2d219592cebf",
              IP_ItemList: [{
                IP: "102.47.35.63", 
                CreatedOn: "2020-10- 31T05:19:30Z",
              }]
            }
          ];

          
          this.visible.length = response.data.UserBrowser.length
          modalSize = response.data.UserBrowser

          modalSize.forEach((element, index) => {
            // this.modalVisible[index] = false
            this.modalVisible = false
          })
          
        }
      } catch (e) {
        
      }
    },
    async deleteDevice(id) {
      try {
        let payload = {
          DeviceId: id,
        }

        const response = await this.$axios({
          method: 'delete',
          url: this.$config.FINANCE_API_URL + '/DeleteBrowser',
          data: payload,
          headers: {
            Authorization: 'Bearer ' + this.$cookies.get('Token'),
            'Content-Type': 'application/json',
          },
        })

        if (response.data.Success) {
          this.getDeviceManagementData()
        }
      } catch (e) {
        
      }
    },
    getSubItems(arr) {
      if (arr.length === 1) {
        return []
      }
      return arr.slice(1)
    },
    expandIcon(props) {
        const close = (
          <span>
            <svg
              id="Component_3_39"
              data-name="Component 3 – 39"
              xmlns="http://www.w3.org/2000/svg"
              width="9"
              height="5"
              viewBox="0 0 7 4"
            >
              <path
                id="Polygon_5"
                data-name="Polygon 5"
                d="M3.5,0,7,4H0Z"
                transform="translate(7 4) rotate(180)"
                fill="#858e9d"
              />
            </svg>
          </span>
        )

        const open = (
          <span>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="9"
              height="5"
              viewBox="0 0 7 4"
            >
              <g
                id="Component_24"
                data-name="Component 24"
                transform="translate(7 4) rotate(180)"
              >
                <path
                  id="Polygon_5"
                  data-name="Polygon 5"
                  d="M3.5,0,7,4H0Z"
                  transform="translate(7 4) rotate(180)"
                  fill="#858e9d"
                />
              </g>
            </svg>
          </span>
        )

        const icon = props.expanded ? open : close

        return (
          <a
            class="expand-row-icon"
            onClick={(e) => props.onExpand(props.record, e)}
            style={{ color: 'blue', cursor: 'pointer' }}
          >
            {icon}
          </a>
        )
    },

  },
}
